<template>
    <section class="buttomlList">
       <div class="nav">
         <router-link to="/questions"></router-link>
         <router-link to="/cooperation"></router-link>
         <router-link to="/privacy"></router-link>
         <router-link to="/contact"></router-link>
       </div>
       <div class="content">
          <router-view></router-view>
       </div>

    </section>
</template>

<script>

    const questionsComponent = {
    template:`<section id="questions">
                <h2></h2>
            </section>`,
    };

    const cooperationComponent = {
    template:`<section id="cooperation">
                <h2></h2>
            </section>`
    };

    const privacyComponent = {
    template:`<section id="privacy">
                <h2></h2>
            </section>`
    };

    const contactComponent = {
    template:`<section id="contact">
                <h2></h2>
            </section>`
    };

    import Vue from 'vue/dist/vue.js';
    import VueRouter from 'vue-router';
    Vue.use(VueRouter);

    //router
    const router = new VueRouter({
      //routes
      routes:[
          //3
          {
              path:"/questions",
              component:questionsComponent
          },
          {
              path:"/cooperation",
              component:cooperationComponent
          },
          {
              path:"/privacy",
              component:privacyComponent
          },
          {
              path:"/contact",
              component:contactComponent
          },
      ]
    });

    export default {
        router,
    };

</script>

<style lang="scss" scoped>
@import "../../scss/common.scss";
.buttomlList
{
    .content{
        >section{
            z-index: 8;
            position: fixed; top:$headerHeight; left: 0;
            width: 100%; height: 100%;
            background: red;
        }
    }
}
</style>



